"use client"

import React, { useState, useEffect } from "react"
import { Document, Page, pdfjs } from "react-pdf"
import { Button } from "@/components/ui/button"
import { 
  ChevronLeft, 
  ChevronRight, 
  Download, 
  File, 
  AlertCircle, 
  Loader2,
  ExternalLink,
  FileText
} from "lucide-react"

// Set the workerSrc for pdf.js
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.mjs`

interface PdfViewerProps {
  fileUrl: string
  pageNumber: number
  onPageChange: (page: number) => void
}

// Supported document types
const SUPPORTED_FORMATS = {
  pdf: ['.pdf'],
  word: ['.doc', '.docx', '.docm', '.dot', '.dotx'],
  excel: ['.xls', '.xlsx', '.xlsm', '.xlt', '.xltx'],
  powerpoint: ['.ppt', '.pptx', '.pptm', '.pot', '.potx'],
  text: ['.txt', '.rtf', '.md'],
  image: ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp']
}

export default function PdfViewer({ fileUrl, pageNumber, onPageChange }: PdfViewerProps) {
  const [numPages, setNumPages] = useState<number | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [fileType, setFileType] = useState<string>("")
  const [fileCategory, setFileCategory] = useState<string>("")

  useEffect(() => {
    if (!fileUrl) {
      setError("No file URL provided")
      setLoading(false)
      return
    }

    // Extract file extension
    const extension = fileUrl.substring(fileUrl.lastIndexOf('.')).toLowerCase()
    setFileType(extension)
    
    // Determine file category
    let category = "unknown"
    for (const [cat, exts] of Object.entries(SUPPORTED_FORMATS)) {
      if (exts.includes(extension)) {
        category = cat
        break
      }
    }
    setFileCategory(category)
    
    console.log("File detected:", { extension, category })
    
    // Reset state
    setLoading(true)
    setError(null)

    // PDF will be loaded by react-pdf component
    // Other file types will show viewer options
    setTimeout(() => setLoading(false), 500)
  }, [fileUrl])

  const onDocumentLoadSuccess = ({ numPages }: { numPages: number }) => {
    console.log("PDF loaded successfully. Number of pages:", numPages);
    setNumPages(numPages)
    setLoading(false)
    setError(null)
  }

  const onDocumentLoadError = (err: Error) => {
    console.error("Failed to load PDF document:", err)
    setError(`Failed to load PDF document. ${err.message}`)
    setLoading(false)
  }

  const handleDownload = () => {
    const link = document.createElement('a')
    link.href = fileUrl
    link.download = fileUrl.substring(fileUrl.lastIndexOf('/') + 1)
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  const goToPrevPage = () => {
    onPageChange(Math.max(pageNumber - 1, 1))
  }

  const goToNextPage = () => {
    onPageChange(Math.min(pageNumber + 1, numPages || 1))
  }

  // Generate Google Docs Viewer URL
  const getGoogleDocsViewerUrl = () => {
    return `https://docs.google.com/viewer?url=${encodeURIComponent(fileUrl)}&embedded=true`
  }

  // Generate Microsoft Office Online URL
  const getOfficeOnlineUrl = () => {
    return `https://view.officeapps.live.com/op/embed.aspx?src=${encodeURIComponent(fileUrl)}`
  }

  // Get appropriate viewer based on file type
  const getViewerUrl = () => {
    if (SUPPORTED_FORMATS.word.includes(fileType)) {
      return getOfficeOnlineUrl()
    } else if (SUPPORTED_FORMATS.excel.includes(fileType)) {
      return getOfficeOnlineUrl()
    } else if (SUPPORTED_FORMATS.powerpoint.includes(fileType)) {
      return getOfficeOnlineUrl()
    } else {
      return getGoogleDocsViewerUrl()
    }
  }

  console.log("PdfViewer state:", { fileUrl, fileType, fileCategory, pageNumber, numPages, loading, error });

  // Show loading state
  if (loading) {
    return (
      <div className="h-full flex flex-col items-center justify-center p-8">
        <Loader2 className="w-12 h-12 text-primary animate-spin mb-4" />
        <p className="text-lg text-muted-foreground">Loading document...</p>
        <p className="text-sm text-muted-foreground mt-2">
          File type: {fileType.toUpperCase().replace('.', '') || "Detecting..."}
        </p>
      </div>
    )
  }

  // Handle PDF files
  if (fileCategory === 'pdf') {
    return (
      <div className="flex flex-col items-center space-y-4">
        <div className="w-full max-w-3xl border border-border rounded-lg overflow-hidden shadow-lg bg-white">
          <Document
            file={fileUrl}
            onLoadSuccess={onDocumentLoadSuccess}
            onLoadError={onDocumentLoadError}
            className="w-full"
            loading={
              <div className="flex items-center justify-center p-12">
                <Loader2 className="w-8 h-8 text-primary animate-spin" />
              </div>
            }
          >
            <Page 
              pageNumber={pageNumber} 
              width={800}
              renderTextLayer={false}
              renderAnnotationLayer={false}
            />
          </Document>
        </div>
        
        <div className="flex items-center justify-between w-full max-w-3xl">
          <div className="flex items-center space-x-4">
            <Button onClick={goToPrevPage} disabled={pageNumber <= 1} variant="outline">
              <ChevronLeft className="h-4 w-4 mr-2" /> Previous
            </Button>
            <p className="text-sm text-muted-foreground">
              Page {pageNumber} of {numPages || "..."}
            </p>
            <Button onClick={goToNextPage} disabled={pageNumber >= (numPages || 1)} variant="outline">
              Next <ChevronRight className="h-4 w-4 ml-2" />
            </Button>
          </div>
          
          <Button onClick={handleDownload} variant="secondary" className="gap-2">
            <Download className="w-4 h-4" />
            Download PDF
          </Button>
        </div>
      </div>
    )
  }

  // Handle Office documents (Word, Excel, PowerPoint)
  if (['word', 'excel', 'powerpoint'].includes(fileCategory)) {
    const viewerUrl = getViewerUrl()
    const documentType = fileCategory.charAt(0).toUpperCase() + fileCategory.slice(1)
    
    return (
      <div className="flex flex-col items-center space-y-6">
        <div className="w-full max-w-4xl border border-border rounded-lg overflow-hidden shadow-lg bg-white">
          <div className="bg-gray-50 dark:bg-gray-800 p-4 border-b border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <FileText className="w-5 h-5 text-primary" />
                <span className="font-medium">
                  {documentType} Document Viewer ({fileType.toUpperCase()})
                </span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 rounded-full bg-red-500"></div>
                <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                <div className="w-3 h-3 rounded-full bg-green-500"></div>
              </div>
            </div>
          </div>
          
          <div className="h-[600px]">
            <iframe
              src={viewerUrl}
              title={`${documentType} Document Viewer`}
              className="w-full h-full border-0"
              sandbox="allow-scripts allow-same-origin"
              loading="lazy"
            />
          </div>
        </div>
        
        <div className="flex flex-col sm:flex-row items-center justify-between w-full max-w-4xl gap-4">
          <div className="flex items-center gap-4">
            <Button onClick={handleDownload} variant="default" className="gap-2">
              <Download className="w-4 h-4" />
              Download {fileType.toUpperCase().replace('.', '')}
            </Button>
            
            <Button 
              variant="outline" 
              className="gap-2"
              asChild
            >
              <a href={viewerUrl} target="_blank" rel="noopener noreferrer">
                <ExternalLink className="w-4 h-4" />
                Open in New Tab
              </a>
            </Button>
          </div>
          
          <div className="text-sm text-muted-foreground">
            Powered by Microsoft Office Online Viewer
          </div>
        </div>
        
        {/* Note about viewer */}
        <div className="w-full max-w-4xl p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg border border-blue-200 dark:border-blue-800">
          <div className="flex items-start gap-3">
            <AlertCircle className="w-5 h-5 text-blue-600 dark:text-blue-400 mt-0.5" />
            <div className="text-sm text-blue-700 dark:text-blue-300">
              <p className="font-medium mb-1">Note about document viewing:</p>
              <p>
                This document is displayed using Microsoft Office Online Viewer. 
                Some formatting may appear differently than in the original application. 
                For full features, download the file and open it in Microsoft Office.
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  // Handle images
  if (fileCategory === 'image') {
    return (
      <div className="flex flex-col items-center space-y-6">
        <div className="w-full max-w-4xl border border-border rounded-lg overflow-hidden shadow-lg bg-white">
          <div className="bg-gray-50 dark:bg-gray-800 p-4 border-b border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <FileText className="w-5 h-5 text-primary" />
                <span className="font-medium">
                  Image Viewer ({fileType.toUpperCase()})
                </span>
              </div>
            </div>
          </div>
          
          <div className="flex items-center justify-center p-8">
            <img
              src={fileUrl}
              alt="Document Image"
              className="max-w-full max-h-[500px] object-contain rounded-lg shadow-lg"
              onError={(e) => {
                const target = e.target as HTMLImageElement
                target.src = '/placeholder-image.jpg'
              }}
            />
          </div>
        </div>
        
        <div className="flex items-center justify-between w-full max-w-4xl">
          <Button onClick={handleDownload} variant="default" className="gap-2">
            <Download className="w-4 h-4" />
            Download Image
          </Button>
          
          <div className="text-sm text-muted-foreground">
            {fileType.toUpperCase()} Image • View only
          </div>
        </div>
      </div>
    )
  }

  // Handle text files
  if (fileCategory === 'text') {
    const [textContent, setTextContent] = useState<string>("")
    
    useEffect(() => {
      if (fileCategory === 'text') {
        fetch(fileUrl)
          .then(response => response.text())
          .then(text => setTextContent(text.slice(0, 10000))) // Limit to 10k chars
          .catch(err => {
            console.error("Failed to load text file:", err)
            setTextContent("Unable to load text content. Please download the file to view.")
          })
      }
    }, [fileUrl, fileCategory])

    return (
      <div className="flex flex-col items-center space-y-6">
        <div className="w-full max-w-4xl border border-border rounded-lg overflow-hidden shadow-lg bg-white">
          <div className="bg-gray-50 dark:bg-gray-800 p-4 border-b border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <FileText className="w-5 h-5 text-primary" />
                <span className="font-medium">
                  Text File Viewer ({fileType.toUpperCase()})
                </span>
              </div>
            </div>
          </div>
          
          <div className="p-6 h-[600px] overflow-auto">
            <pre className="font-mono text-sm whitespace-pre-wrap break-words">
              {textContent || "Loading text content..."}
            </pre>
          </div>
        </div>
        
        <div className="flex items-center justify-between w-full max-w-4xl">
          <Button onClick={handleDownload} variant="default" className="gap-2">
            <Download className="w-4 h-4" />
            Download Text File
          </Button>
          
          <div className="text-sm text-muted-foreground">
            {fileType.toUpperCase()} • Plain text viewer
          </div>
        </div>
      </div>
    )
  }

  // Default view for unsupported files
  return (
    <div className="h-full flex flex-col items-center justify-center p-8 bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      <div className="text-center space-y-6 max-w-md">
        <div className="w-20 h-20 mx-auto rounded-xl bg-primary/10 flex items-center justify-center">
          <File className="w-10 h-10 text-primary" />
        </div>
        
        <div>
          <h3 className="text-2xl font-bold mb-2">Document Format</h3>
          <p className="text-muted-foreground">
            This {fileType.toUpperCase()} file can't be previewed inline.
          </p>
        </div>

        <div className="space-y-3">
          <Button
            onClick={handleDownload}
            className="w-full py-6 gap-3"
            size="lg"
          >
            <Download className="w-5 h-5" />
            Download {fileType.toUpperCase().replace('.', '')} File
          </Button>
          
          <p className="text-sm text-muted-foreground">
            File format: {fileType.toUpperCase()} • Size: Unknown
          </p>
        </div>

        <div className="pt-6 border-t border-gray-200 dark:border-gray-700">
          <h4 className="font-medium mb-2">Supported formats for inline viewing:</h4>
          <ul className="text-sm text-muted-foreground space-y-1">
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>PDF (.pdf) - Native preview</span>
            </li>
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>Word (.doc, .docx) - Office Online Viewer</span>
            </li>
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>Excel (.xls, .xlsx) - Office Online Viewer</span>
            </li>
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>PowerPoint (.ppt, .pptx) - Office Online Viewer</span>
            </li>
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>Images (.jpg, .png, etc.) - Image viewer</span>
            </li>
            <li className="flex items-center gap-2">
              <span className="text-green-500">✓</span>
              <span>Text files (.txt, .rtf) - Text viewer</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}
